  height: 30px;
  line-height: 30px; }

.btn {
  margin: 0;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  display: inline-block;
  min-width: 62px;
  padding: 0.6rem 1.2rem;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  transition: background-color 200ms ease-in, color 200ms ease-out;
  will-change: color, background-color;
  color: black; }
  .btn:focus {
    outline: none; }
  .btn--primary {
    background-color: #515153;
    color: white; }
    .btn--primary:hover {
      background-color: #242425;
      color: white; }
  .btn--secondary {
    border: 1px solid whitesmoke; }
    .btn--secondary:hover {
      background-color: whitesmoke;
      color: black; }

.cover {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50vh;
  background-size: cover; }

.header {
  width: 100%; }
  .header__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center; }
  .header__brand {
    padding: 60px 0 0 0; }
    .header__brand > a > img {
      width: 232px;
      height: 83px; }

.nav {
  padding: 20px 0; }
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0; }
    .nav__list-item > a {
      position: relative;
      display: block;
      margin-right: 2rem;
      padding: 1rem 0;
      font-size: 18px;
      font-family: "Montserrat", sans-serif;
      color: black;
      text-decoration: none; }
      .nav__list-item > a:after {
        content: "";
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 0;
        width: 100%;
        height: 8px;
        background-color: rgba(102, 51, 153, 0.4);
        transform: scaleX(0);
        transform-origin: left;
        transition: transform 200ms ease-in; }
      .nav__list-item > a:hover:after {
        transform: scaleX(1); }
    .nav__list-item:last-child > a {
      margin-right: 0; }

.content {
  max-width: 1290px;
  margin: 0 auto;
  font-family: "Playfair Display", serif; }

.loop {
  margin: 60px 0; }
  .loop__posts {
    display: flex;
    flex-wrap: wrap;
    width: 100%; }
  .loop__post {
    display: flex;
    flex: 0 0 100%;
    margin-bottom: 60px;
    background-color: white;
    border-radius: 4px; }
    .loop__post:last-child {
      margin-bottom: 0; }
    .loop__post-cover {
      flex: 0 0 60%;
      background-size: cover;
      background-position: center center;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px; }
    .loop__post-body {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 0 0 40%;
      padding: 20px 30px; }
    .loop__post-tags {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      list-style: none;
      margin: 0;
      padding: 0; }
    .loop__post-tag {
      font-size: 12px;
      text-transform: uppercase;
      font-family: "Playfair Display", serif; }
    .loop__post-title {
      text-align: center;
      margin: 0; }
      .loop__post-title > a {
        font-family: "Playfair Display", serif;
        letter-spacing: 1.0px;
        text-decoration: none;
        font-size: 36px;
        color: black; }
    .loop__post-meta {
      display: flex;
      justify-content: center;
      width: 100%; }
      .loop__post-meta > em {
        position: relative;
        display: block;
        margin: 0 5px;
        padding: 0 0 0 0;
        text-transform: lowercase;
        font-size: 10px; }
    .loop__post-author {
      text-transform: uppercase;
      font-weight: normal;
      font-size: 12px; }
    .loop__post-date {
      display: block;
      font-size: 11px;
      text-transform: uppercase; }
    .loop__post-excerpt {
      margin: 0;
      padding: 20px 30px;
      font-family: "Playfair Display", serif; }

.loop__post.featured {
  position: relative;
  justify-content: center;
  align-items: center; }
  .loop__post.featured .loop__post-cover {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    left: 0;
    flex: 0 0 100%;
    height: 600px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-left-radius: 0; }
  .loop__post.featured .loop__post-body {
    display: flex;
    flex: 0 0 auto;
    width: 100%;
    max-width: 700px;
    margin-top: 600px;
    z-index: 1;
    background-color: white; }

.footer {
  background: white;
  padding: 30px 20px;
  text-align: center; }

* {
  box-sizing: border-box; }

body {
  background-color: #f6f4f3; }
